@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: 'Roboto', sans-serif;
  Font-style: Regular;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 62px;
  width: 100vw;
  height: 68px;
  background-color: #2196f3;
  margin-bottom: 25px;
}

.header-item {
  display: inline-block;
  font-family: Roboto;
  font-style: Regular;
  font-size: 14px;
  line-height: 21px;
  line-height: 128%;
  padding: 25px;
  color: rgba(240, 248, 255, 0.7);
}

.header-item_active {
  background-color: #1e88e5;
  color: rgba(240, 248, 255, 1);
  max-height: 68px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.AllCats-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.catItem {
  overflow: hidden;
  margin: 24px;
  position: relative;
  width: 225px;
  height: 225px;
}

.catPhoto {
  width: 225px;
  height: 225px;
  object-fit: cover;
}

.catItem:hover {
  transform: scale(1.1);
  box-shadow: 0px 5px 6px 2px rgba(0, 0, 0, 0.34);
}

.heart {
  position: absolute;
  top: 70%;
  left: 70%;
  width: 48px;
  height: 36.7px;
}
